import React, { useEffect, useState } from 'react'
import { Link, useLocation, useNavigate } from 'react-router-dom'
import appendScript from "../../utils/appendScript"
import removeScript from "../../utils/removeScript"
import swal from 'sweetalert';
import { tKey } from "../../tkey"
import { useDispatch, useSelector } from 'react-redux';
import { magic } from '../../lib/magic';
import { ethers } from 'ethers';
import { setWeb3 } from '../../reducers/web3Reducer';
import { login } from '../../actions/authAction';
import { AVAILABLE_NETWORK } from '../../actions/constant';




const Login = () => {

    const { key } = useLocation()
    const dispatch = useDispatch()
    const navigate = useNavigate()
    useEffect(() => {
        appendScript("./assets/js/vendor.min.js")
        appendScript("./assets/js/theme.min.js")

        return () => {
            removeScript("./assets/js/vendor.min.js")
            removeScript("./assets/js/theme.min.js")
        }

    }, [key])

    const { user, isAuthenticated } = useSelector(state => state.auth)
    const [email, setemail] = useState()
    const { connected, providerConnect, provider, signer } = useSelector(state => state.web3)

    console.log("connected", connected)


    useEffect(() => {
        if (isAuthenticated) {
            navigate("/user/dashboard")

        }
    }, [isAuthenticated])

    const userLogin = async () => {


        const providerConnect = new ethers.providers.Web3Provider(magic.rpcProvider);
        const { chainId } = await providerConnect.getNetwork()
        localStorage.setItem("chainId", chainId)
        console.log("chainId", chainId)
        const signer = providerConnect.getSigner();
        const address = await signer.getAddress();

        var findNetwork = AVAILABLE_NETWORK.find(x => x.networkID === chainId)

        const web3Data = {
            //  providerConnect: providerConnect,
            provider: providerConnect,
            address: address,
            signer: signer,
            connected: true,
            chainId: chainId,
            symbol: findNetwork.symbol,
            networkName: findNetwork.networkName,
            icon: findNetwork.icon
        }

        const userInfo = {
            firstName: "~",
            email: email,
            origin: "Email",
            walletAddress: address
        }

        dispatch(setWeb3(web3Data))
        dispatch(login(userInfo, "user"))

    }

    const onEmailLogin = async () => {
        try {
            const resultData = await magic.auth.loginWithEmailOTP({ email });
            userLogin()
        } catch (err) {
            console.error(err);
        }

    }



    const onGoogleLogin = async () => {
        try {
            await magic.oauth.loginWithRedirect({
                provider: "google",
                redirectURI: new URL("/auth", window.location.origin).href,
            });
        } catch (err) {
            console.error(err);
        }
    }



    return (
        <div className="bg-gradient2 min-vh-100 align-items-center d-flex justify-content-center pt-2 pt-sm-5 pb-4 pb-sm-5">
            <div className="container">
                <div className="row justify-content-center">
                    <div className="col-xl-12">
                        <div className="card">
                            <div className="card-body p-0">
                                <div className="row g-0">
                                    <div className="col-md-5 shadow">
                                        <div className="p-xl-5 p-3">
                                            <div className="mx-auto mb-5">
                                                <a href="/" className="d-flex">
                                                    <img
                                                        src="assets/images/logo.png"
                                                        className="align-self-center"
                                                        alt="logo-img"
                                                        height={50}
                                                    />
                                                </a>
                                            </div>
                                            <h6 className="h5 mb-0 mt-3">With a account you get access to easy payments all around the world</h6>
                                            <p className="text-muted mt-1 mb-4">
                                                Sign in with email

                                            </p>
                                            {/*form start*/}
                                            <form action="#" className="authentication-form">
                                                <div className="mb-3">
                                                    <label
                                                        htmlFor="exampleInputEmail1"
                                                        className="form-label"
                                                    >
                                                        Email <small>*</small>
                                                    </label>
                                                    <input
                                                        type="email"
                                                        className="form-control"
                                                        id="exampleInputEmail1"
                                                        placeholder="Email"
                                                        name="email"
                                                        onChange={(e) => setemail(e.target.value)}
                                                        value={email}
                                                    />
                                                </div>

                                                <div className="mb-0 text-center d-grid">
                                                    <button className="btn btn-primary " type="button" onClick={onEmailLogin}>
                                                        Sign In
                                                    </button>
                                                </div>
                                            </form>
                                            {/*/.form end*/}
                                            <div className="py-3 text-center">
                                                <span className="fs-13 fw-bold">OR</span>
                                            </div>
                                            <div className="row">
                                                <div className="col-12 text-center mt-2">
                                                    <button onClick={onGoogleLogin} className="btn btn-primary  w-100">
                                                        <i
                                                            data-feather="mail"
                                                            className="icon icon-xs me-2"
                                                        />
                                                        Sign in with Google
                                                    </button>
                                                </div>

                                                <div className="col-12 text-center mt-2">
                                                    <a href="" className="btn btn-white opacity-50 w-100">
                                                        <i
                                                            data-feather="facebook"
                                                            className="icon icon-xs me-2"
                                                        />
                                                        Sign in with Facebook
                                                    </a>
                                                </div>

                                                <div className="col-12 text-center mt-2">
                                                    <a href="" className="btn btn-white opacity-50 w-100">
                                                        <i
                                                            data-feather="twitter"
                                                            className="icon icon-xs me-2"
                                                        />
                                                        Sign in with Twitter
                                                    </a>
                                                </div>

                                                <div className="col-12 text-center mt-2">
                                                    <a href="" className="btn btn-white opacity-50 w-100">
                                                        <i
                                                            data-feather="github"
                                                            className="icon icon-xs me-2"
                                                        />
                                                        Sign in with Github
                                                    </a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-md-5 offset-md-1 d-none d-md-inline-block">
                                        <div className="position-relative mt-5 pt-5">
                                            {/*slider start*/}
                                            <div className="slider">
                                                <div
                                                    className="swiper-container"
                                                    data-toggle="swiper"
                                                    data-swiper='{"slidesPerView":1, "loop":true, "spaceBetween": 0, "autoplay": {"delay": 5000}, "breakpoints": {"576": {"slidesPerView": 1.2 }, "768": { "slidesPerView": 1 } }, "roundLengths":true, "pagination": {"el": ".swiper-pagination", "dynamicBullets": true}}'
                                                >
                                                    <div className="swiper-wrapper">
                                                        <div className="swiper-slide">
                                                            <div className="swiper-slide-content">
                                                                <div className="row text-center">
                                                                    <div className="col">
                                                                        <img
                                                                            src="./assets/images/products/ecommerce.svg"
                                                                            alt=""
                                                                            className="w-75"
                                                                        />
                                                                    </div>
                                                                </div>
                                                                <div className="row text-center my-4 pb-5">
                                                                    <div className="col">
                                                                        <h5 className="fw-medium fs-16">
                                                                            E-commerce plugins
                                                                        </h5>
                                                                        <p className="text-muted">
                                                                            Now, you can sell your products for Crypto by using Paytez button
                                                                        </p>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        {/* swiper-slide End */}
                                                        <div className="swiper-slide">
                                                            <div className="swiper-slide-content">
                                                                <div className="row text-center">
                                                                    <div className="col">
                                                                        <img
                                                                            src="./assets/images/products/qrpayment.svg"
                                                                            alt=""
                                                                            className="w-75"
                                                                        />
                                                                    </div>
                                                                </div>
                                                                <div className="row text-center my-4 pb-5">
                                                                    <div className="col">
                                                                        <h5 className="fw-medium fs-16">
                                                                            QR Payments
                                                                        </h5>
                                                                        <p className="text-muted">
                                                                            Using QR Payment, now you get the settlement done.
                                                                        </p>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        {/* swiper-slide End */}
                                                        <div className="swiper-slide">
                                                            <div className="swiper-slide-content">
                                                                <div className="row text-center">
                                                                    <div className="col">
                                                                        <img
                                                                            src="./assets/images/products/api.svg"
                                                                            alt=""
                                                                            className="w-75"
                                                                        />
                                                                    </div>
                                                                </div>
                                                                <div className="row text-center my-4 pb-5">
                                                                    <div className="col">
                                                                        <h5 className="fw-medium fs-16">
                                                                            API integration
                                                                        </h5>
                                                                        <p className="text-muted">
                                                                            Well documented developer docs for the customer who wants to integrate our API's
                                                                        </p>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        {/* swiper-slide End */}
                                                    </div>
                                                    {/* swiper-wrapper End */}
                                                    <div className="swiper-pagination" />
                                                </div>
                                            </div>
                                            {/*slider end*/}
                                        </div>
                                    </div>
                                </div>
                            </div>{" "}
                            {/* end card-body */}
                        </div>
                        {/* end card */}
                    </div>{" "}
                    {/* end col */}
                </div>
                {/* end row */}
            </div>
            {/* end container */}
        </div>

    )
}

export default Login
